.header__toggle {
  width: 40px;
  height: 20px;
  cursor: pointer;
  background-color: #fff;
  border: .604701px solid #fd5103;
  border-radius: 20px;
  transition: all .3s;
  display: block;
  position: relative;
}

.header__toggle:after {
  content: "";
  width: 16px;
  height: 16px;
  background-color: #fd5103;
  border-radius: 50%;
  transition: all .3s;
  position: absolute;
  top: 1px;
  left: 1px;
}

input:checked ~ .header__toggle {
  background-color: #fd5103;
  border-color: #fff;
  transition: all .3s;
}

input:checked ~ .header__toggle:after {
  background-color: #fff;
  left: calc(100% - 1px);
  transform: translateX(-100%);
}

.header__toggle-light, .header__toggle-dark {
  font-family: Poppins;
  font-size: 18px;
  line-height: 24px;
}

